.navbar-new {
    z-index: 3;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    overflow: hidden;
    background-color: rgb(255, 255, 255);
    width: 100%;
    font-family: 'Work Sans', sans-serif;
    font-size: 2rem;
    padding: 0 4vw;
}

.navbar-icon p {
    padding-left: 1vh;
    font-size: 2.5rem;
    margin: 0vh;
    opacity: 1;
    -webkit-transition: 0.4s;

    &:hover {
        cursor: pointer;
        opacity: 0.6;
        transform: scale(1.15);
        transition: all 0.4s ease-in-out;
        
    }
}

.contact-nav {
    padding-right: 8vh;
}

ul {
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    margin: 0;
    padding-right: 5vh;
    
}

li {
    padding: 0.5vh 1.5vh;
    -webkit-transition: 0.4s;
    
    &:hover {
        cursor: pointer;
        transform: scale(1.1);
        transition: 0.4s;
    }

}

.contact-nav a {
    text-decoration: none;
}

.text-link{
    // color: blue;
} 
.text-link:hover {
    cursor: pointer;
    transform: scale(1.1);
    transition: 0.4s;
}


